.fonts {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    &__font {
        flex-basis: 18%;
        text-align: center;
        margin-bottom: 30px;
    }

    .btn-block {
        width: 100%;
    }

    &__title {
        font-size: 22px;
        font-style: normal;
        font-weight: 700;
        letter-spacing: 0em;
        text-align: center;
        margin-bottom: 20px;
    }

    .raleway {
        font-family: "Raleway", sans-serif;
        font-weight: 800
    }

    .montserrat {
        font-family: 'Montserrat', sans-serif;

    }

    .playfair {
        font-family: 'Playfair-Display', serif;

    }

    .roboto {
        font-family: 'Roboto', sans-serif;

    }

    .opensans {
        font-family: 'Open-Sans', sans-serif;

    }


}

@media only screen and (max-width : 1420px) {
    .fonts {

        &__font {
            flex-basis: 32%;
        }
    }
}

@media only screen and (max-width : 590px) {
    .fonts {

        &__font {
            flex-basis: 48%;
        }
    }
}
